"use client";

import { useState } from "react";
import Navbar from "../components/Navbar";

export default function Iletisim() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    subject: "",
    message: "",
  });
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    await new Promise((resolve) => setTimeout(resolve, 1500));
    setSuccess(true);
    setLoading(false);
    setFormData({ name: "", email: "", phone: "", subject: "", message: "" });
    setTimeout(() => setSuccess(false), 5000);
  };

  return (
    <>
      <Navbar />
      <main className="min-h-screen px-6 py-24 pt-[88px]">
        <div className="mx-auto max-w-[980px]">
          
          <div className="mb-16 text-center">
            <h1 className="animate-fade-in-up mb-6 text-[48px] font-semibold leading-[1.05] tracking-[-0.025em] text-[#1d1d1f] sm:text-[56px] lg:text-[72px]">
              İletişim
            </h1>
            <p className="animate-fade-in-up animate-delay-100 mx-auto max-w-[640px] text-[21px] leading-[1.47] tracking-[-0.01em] text-[#6e6e73] sm:text-[24px]">
              Size yardımcı olmak için buradayız.
            </p>
          </div>

          <div className="mx-auto max-w-[640px]">
            
            <div className="animate-fade-in-up animate-delay-200 rounded-3xl bg-white p-8 shadow-sm ring-1 ring-black/[0.04] sm:p-10">
              {success && (
                <div className="mb-8 flex items-center gap-3 rounded-2xl bg-[#34c759]/10 p-4">
                  <svg className="h-5 w-5 text-[#34c759]" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
                    <path strokeLinecap="round" strokeLinejoin="round" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  <p className="text-[15px] font-medium text-[#34c759]">Mesajınız başarıyla gönderildi!</p>
                </div>
              )}

              <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                  <label htmlFor="name" className="mb-2 block text-[15px] font-medium text-[#1d1d1f]">
                    Ad Soyad
                  </label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    required
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    className="h-[52px] w-full rounded-xl border border-[#d2d2d7] bg-white px-4 text-[17px] text-[#1d1d1f] outline-none transition-all duration-200 placeholder:text-[#86868b] hover:border-[#aeaeb2] focus:border-[#0071e3] focus:ring-4 focus:ring-[#0071e3]/12"
                    placeholder="Adınız ve soyadınız"
                  />
                </div>

                <div>
                  <label htmlFor="email" className="mb-2 block text-[15px] font-medium text-[#1d1d1f]">
                    E-posta
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    required
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    className="h-[52px] w-full rounded-xl border border-[#d2d2d7] bg-white px-4 text-[17px] text-[#1d1d1f] outline-none transition-all duration-200 placeholder:text-[#86868b] hover:border-[#aeaeb2] focus:border-[#0071e3] focus:ring-4 focus:ring-[#0071e3]/12"
                    placeholder="ornek@mail.com"
                  />
                </div>

                <div>
                  <label htmlFor="phone" className="mb-2 block text-[15px] font-medium text-[#1d1d1f]">
                    Telefon
                  </label>
                  <input
                    type="tel"
                    id="phone"
                    name="phone"
                    value={formData.phone}
                    onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                    className="h-[52px] w-full rounded-xl border border-[#d2d2d7] bg-white px-4 text-[17px] text-[#1d1d1f] outline-none transition-all duration-200 placeholder:text-[#86868b] hover:border-[#aeaeb2] focus:border-[#0071e3] focus:ring-4 focus:ring-[#0071e3]/12"
                    placeholder="0555 123 45 67"
                  />
                </div>

                <div>
                  <label htmlFor="subject" className="mb-2 block text-[15px] font-medium text-[#1d1d1f]">
                    Konu
                  </label>
                  <input
                    type="text"
                    id="subject"
                    name="subject"
                    required
                    value={formData.subject}
                    onChange={(e) => setFormData({ ...formData, subject: e.target.value })}
                    className="h-[52px] w-full rounded-xl border border-[#d2d2d7] bg-white px-4 text-[17px] text-[#1d1d1f] outline-none transition-all duration-200 placeholder:text-[#86868b] hover:border-[#aeaeb2] focus:border-[#0071e3] focus:ring-4 focus:ring-[#0071e3]/12"
                    placeholder="Mesajınızın konusu"
                  />
                </div>

                <div>
                  <label htmlFor="message" className="mb-2 block text-[15px] font-medium text-[#1d1d1f]">
                    Mesajınız
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    rows={5}
                    required
                    value={formData.message}
                    onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                    className="w-full resize-none rounded-xl border border-[#d2d2d7] bg-white p-4 text-[17px] text-[#1d1d1f] outline-none transition-all duration-200 placeholder:text-[#86868b] hover:border-[#aeaeb2] focus:border-[#0071e3] focus:ring-4 focus:ring-[#0071e3]/12"
                    placeholder="Mesajınızı buraya yazın..."
                  />
                </div>

                <button
                  type="submit"
                  disabled={loading}
                  className="relative flex h-[52px] w-full items-center justify-center rounded-xl bg-[#0071e3] text-[17px] font-medium text-white transition-all duration-200 hover:bg-[#0077ed] active:scale-[0.98] disabled:cursor-not-allowed disabled:opacity-50"
                >
                  {loading ? (
                    <div className="h-5 w-5 animate-spin rounded-full border-2 border-white/30 border-t-white" />
                  ) : (
                    "Gönder"
                  )}
                </button>
              </form>
            </div>

            <div className="mt-12 grid gap-6 sm:grid-cols-2">
              <div className="animate-fade-in-up animate-delay-300 rounded-2xl bg-[#f5f5f7] p-6 text-center">
                <div className="mx-auto flex h-12 w-12 items-center justify-center rounded-full bg-[#0071e3]/10">
                  <svg className="h-6 w-6 text-[#0071e3]" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
                    <path strokeLinecap="round" strokeLinejoin="round" d="M18.364 5.636l-3.536 3.536m0 5.656l3.536 3.536M9.172 9.172L5.636 5.636m3.536 9.192l-3.536 3.536M21 12a9 9 0 11-18 0 9 9 0 0118 0zm-5 0a4 4 0 11-8 0 4 4 0 018 0z" />
                  </svg>
                </div>
                <h3 className="mt-4 text-[17px] font-semibold text-[#1d1d1f]">
                  Destek
                </h3>
                <p className="mt-1 text-[15px] text-[#0071e3]">
                  destek@anikod.com
                </p>
              </div>

              <div className="animate-fade-in-up animate-delay-400 rounded-2xl bg-[#f5f5f7] p-6 text-center">
                <div className="mx-auto flex h-12 w-12 items-center justify-center rounded-full bg-[#34c759]/10">
                  <svg className="h-6 w-6 text-[#34c759]" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
                    <path strokeLinecap="round" strokeLinejoin="round" d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                  </svg>
                </div>
                <h3 className="mt-4 text-[17px] font-semibold text-[#1d1d1f]">
                  Genel
                </h3>
                <p className="mt-1 text-[15px] text-[#0071e3]">
                  info@anikod.com
                </p>
              </div>
            </div>

          </div>

        </div>
      </main>
    </>
  );
}
